.up {
  z-index: 7;
  cursor: pointer;
  opacity: .5;
  visibility: visible;
  background-color: rgba(0, 0, 0, 0);
  border: 3px solid #ff6b07;
  border-radius: 50%;
  padding: 25px;
  transition: border-color .2s linear, opacity .2s linear, box-shadow .2s linear;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 0 7px 2px #ff6b07;
}

.up:after, .up:before {
  width: 5px;
  height: 22px;
  content: "";
  background-color: #ff6b07;
  border-radius: 10px;
  margin-left: -8px;
  transition: background-color .2s linear;
  position: absolute;
  top: 11px;
  left: 50%;
  transform: rotate(45deg);
}

.up:after {
